'use client';

import { useEffect, useState } from 'react';
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from 'recharts';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/app/loan/components/ui/card';
import { Input } from '@/app/loan/components/ui/input';
import { Button } from '@/app/loan/components/ui/button';
import { Label } from '@/app/loan/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Lock, Mail, LayoutDashboard, FileText, Users, BarChart3, Settings, LogOut, Download, Filter, X, TrendingUp, TrendingDown, UserPlus, Edit, Trash2, Shield, Eye, EyeOff, ChevronRight, MapPin, Plus } from 'lucide-react';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/app/loan/components/ui/table';

interface Application {
  _id: string;
  name: string;
  phone: string;
  loanAmount: string;
  branch: string;
  createdAt: string;
}

interface AdminUser {
  _id: string;
  email: string;
  role?: string;
  createdAt: string;
  isActive: boolean;
}

interface DecodedToken {
  email: string;
  id: string;
  role: string;
  iat?: number;
  exp?: number;
}

export default function AdminPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [token, setToken] = useState<string | null>(null);
  const [userRole, setUserRole] = useState<string | null>(null);
  const [applications, setApplications] = useState<Application[]>([]);
  const [adminUsers, setAdminUsers] = useState<AdminUser[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showCredentialManagement, setShowCredentialManagement] = useState(false);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [editingUser, setEditingUser] = useState<AdminUser | null>(null);
  const [newUserEmail, setNewUserEmail] = useState('');
  const [newUserPassword, setNewUserPassword] = useState('');
  const [editUserEmail, setEditUserEmail] = useState('');
  const [editUserPassword, setEditUserPassword] = useState('');
  const [editUserActive, setEditUserActive] = useState(true);
  const [filterBranch, setFilterBranch] = useState('');
  const [filterLoanAmount, setFilterLoanAmount] = useState('');
  const [showAnalytics, setShowAnalytics] = useState(true);
  const [activeSection, setActiveSection] = useState<'dashboard' | 'applications' | 'analytics' | 'credentials' | 'branches' | 'loan-plans' | 'settings'>('dashboard');
  const [branches, setBranches] = useState<any[]>([]);
  const [branchesLoading, setBranchesLoading] = useState(false);
  const [showCreateBranch, setShowCreateBranch] = useState(false);
  const [newBranchTown, setNewBranchTown] = useState('');
  const [newBranchPincode, setNewBranchPincode] = useState('');
  const [loanPlans, setLoanPlans] = useState<any[]>([]);
  const [loanPlansLoading, setLoanPlansLoading] = useState(false);
  const [showCreatePlan, setShowCreatePlan] = useState(false);
  const [editingPlan, setEditingPlan] = useState<any | null>(null);
  const [interestRate, setInterestRate] = useState<number>(9.0);
  const [interestRateLoading, setInterestRateLoading] = useState(false);
  const [visitorAnalytics, setVisitorAnalytics] = useState<any>(null);
  const [loanAnalytics, setLoanAnalytics] = useState<any>(null);
  const [analyticsLoading, setAnalyticsLoading] = useState(false);
  const [adminUsersLoading, setAdminUsersLoading] = useState(false);

  // Helper function to decode JWT token
  const decodeToken = (token: string): DecodedToken | null => {
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map((c) => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
          .join(''),
      );
      return JSON.parse(jsonPayload);
    } catch (error) {
      console.error('Error decoding token:', error);
      return null;
    }
  };

  const backendBase =
    process.env.NEXT_PUBLIC_BACKEND_URL || 'http://localhost:4000';

  async function parseJsonSafe(res: Response) {
    const contentType = res.headers.get('content-type') || '';
    if (contentType.includes('application/json')) {
      return res.json();
    }
    const text = await res.text();
    console.error('Expected JSON but got:', text.slice(0, 200));
    throw new Error('Unexpected response from server (not JSON)');
  }

  useEffect(() => {
    const storedToken =
      typeof window !== 'undefined'
        ? window.localStorage.getItem('adminToken')
        : null;
    if (storedToken) {
      setToken(storedToken);
      // Decode token to get user role
      const decoded = decodeToken(storedToken);
      if (decoded && decoded.role) {
        setUserRole(decoded.role);
      }
    }
  }, []);

  useEffect(() => {
    if (!token) return;

    const fetchApplications = async () => {
      try {
        setLoading(true);
        setError(null);

        const res = await fetch(`${backendBase}/api/admin/applications`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load applications');
        }

        setApplications(data.applications || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load applications',
        );
      } finally {
        setLoading(false);
      }
    };

    fetchApplications();
  }, [token, backendBase]);

  useEffect(() => {
    if (!token || userRole !== 'super_admin' || activeSection !== 'credentials') return;

    const fetchAdminUsers = async () => {
      try {
        setAdminUsersLoading(true);
        setError(null);
        const res = await fetch(`${backendBase}/api/admin/users`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load admin users');
        }

        setAdminUsers(data.users || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load admin users',
        );
      } finally {
        setAdminUsersLoading(false);
      }
    };

    fetchAdminUsers();
  }, [token, backendBase, userRole, activeSection]);

  useEffect(() => {
    if (!token || !isSuperAdmin) return;

    const fetchAnalytics = async () => {
      try {
        setAnalyticsLoading(true);
        setError(null);

        const [visitorsRes, loansRes] = await Promise.all([
          fetch(`${backendBase}/api/admin/analytics/visitors?days=30`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          fetch(`${backendBase}/api/admin/analytics/loans`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
        ]);

        const visitorsData = await parseJsonSafe(visitorsRes);
        const loansData = await parseJsonSafe(loansRes);

        if (visitorsData.success) {
          setVisitorAnalytics(visitorsData.analytics);
        }
        if (loansData.success) {
          setLoanAnalytics(loansData.analytics);
        }
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load analytics',
        );
      } finally {
        setAnalyticsLoading(false);
      }
    };

    fetchAnalytics();
  }, [token, backendBase, userRole]);

  useEffect(() => {
    if (!token || userRole !== 'super_admin' || activeSection !== 'branches') return;

    const fetchBranches = async () => {
      try {
        setBranchesLoading(true);
        setError(null);
        const res = await fetch(`${backendBase}/api/admin/branches`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load branches');
        }

        setBranches(data.branches || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load branches',
        );
      } finally {
        setBranchesLoading(false);
      }
    };

    fetchBranches();
  }, [token, backendBase, userRole, activeSection]);

  useEffect(() => {
    if (!token || userRole !== 'super_admin' || activeSection !== 'loan-plans') return;

    const fetchLoanPlans = async () => {
      try {
        setLoanPlansLoading(true);
        setError(null);
        const res = await fetch(`${backendBase}/api/admin/loan-plans`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load loan plans');
        }

        setLoanPlans(data.plans || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load loan plans',
        );
      } finally {
        setLoanPlansLoading(false);
      }
    };

    fetchLoanPlans();
  }, [token, backendBase, userRole, activeSection]);

  useEffect(() => {
    if (!token || userRole !== 'super_admin' || activeSection !== 'settings') return;

    const fetchInterestRate = async () => {
      try {
        setInterestRateLoading(true);
        setError(null);
        const res = await fetch(`${backendBase}/api/admin/interest-rate`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load interest rate');
        }

        setInterestRate(data.interestRate || 9.0);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load interest rate',
        );
      } finally {
        setInterestRateLoading(false);
      }
    };

    fetchInterestRate();
  }, [token, backendBase, userRole, activeSection]);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success || !data.token) {
        throw new Error(data.error || 'Invalid email or password');
      }

      setToken(data.token);
      if (typeof window !== 'undefined') {
        window.localStorage.setItem('adminToken', data.token);
        // Decode token to get user role
        const decoded = decodeToken(data.token);
        if (decoded && decoded.role) {
          setUserRole(decoded.role);
        }
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Login failed');
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    setToken(null);
    setUserRole(null);
    setShowCredentialManagement(false);
    if (typeof window !== 'undefined') {
      window.localStorage.removeItem('adminToken');
    }
  };

  // Check if user is super_admin
  const isSuperAdmin = userRole === 'super_admin';

  // Calculate KPI metrics
  const totalApplications = applications.length;
  const recentApplications = applications.filter((app) => {
    const appDate = new Date(app.createdAt);
    const weekAgo = new Date();
    weekAgo.setDate(weekAgo.getDate() - 7);
    return appDate >= weekAgo;
  }).length;
  
  const totalLoanAmount = applications.reduce((sum, app) => {
    const amount = parseFloat(app.loanAmount.replace(/[â‚¹,\s]/g, '')) || 0;
    return sum + amount;
  }, 0);

  // Get unique branches/pincodes for filter dropdown
  const uniqueBranches = Array.from(
    new Set(applications.map((app) => app.branch).filter(Boolean))
  ).sort();

  // Filter applications based on selected filters
  const filteredApplications = applications.filter((app) => {
    const matchesBranch =
      !filterBranch || app.branch.toLowerCase().includes(filterBranch.toLowerCase());
    const matchesLoanAmount =
      !filterLoanAmount ||
      app.loanAmount.toLowerCase().includes(filterLoanAmount.toLowerCase());
    return matchesBranch && matchesLoanAmount;
  });

  const handleClearFilters = () => {
    setFilterBranch('');
    setFilterLoanAmount('');
  };

  const handleCreateUser = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/users`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          email: newUserEmail,
          password: newUserPassword,
        }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to create admin user');
      }

      setNewUserEmail('');
      setNewUserPassword('');
      setShowCreateForm(false);
      
      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to create admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleEditUser = (user: AdminUser) => {
    setEditingUser(user);
    setEditUserEmail(user.email);
    setEditUserPassword('');
    setEditUserActive(user.isActive);
  };

  const handleUpdateUser = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingUser) return;

    setError(null);
    setLoading(true);

    try {
      const updateData: any = {
        email: editUserEmail,
        isActive: editUserActive,
      };
      if (editUserPassword.trim()) {
        updateData.password = editUserPassword;
      }

      const res = await fetch(`${backendBase}/api/admin/users/${editingUser._id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(updateData),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to update admin user');
      }

      setEditingUser(null);
      setEditUserEmail('');
      setEditUserPassword('');
      
      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to update admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteUser = async (userId: string) => {
    if (!confirm('Are you sure you want to delete this admin user?')) return;

    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/users/${userId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to delete admin user');
      }

      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to delete admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleCreateBranch = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newBranchTown || !newBranchPincode) return;

    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/branches`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          town: newBranchTown,
          pincode: newBranchPincode,
        }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to create branch');
      }

      setNewBranchTown('');
      setNewBranchPincode('');
      setShowCreateBranch(false);

      // Refresh branches list
      const branchesRes = await fetch(`${backendBase}/api/admin/branches`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const branchesData = await parseJsonSafe(branchesRes);
      if (branchesData.success) {
        setBranches(branchesData.branches || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to create branch');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteBranch = async (branchId: string) => {
    if (!confirm('Are you sure you want to delete this branch?')) return;

    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/branches/${branchId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to delete branch');
      }

      // Refresh branches list
      const branchesRes = await fetch(`${backendBase}/api/admin/branches`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const branchesData = await parseJsonSafe(branchesRes);
      if (branchesData.success) {
        setBranches(branchesData.branches || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to delete branch');
    } finally {
      setLoading(false);
    }
  };

  // Loan Plan handlers
  const [newPlan, setNewPlan] = useState({
    name: '',
    minAmount: '',
    maxAmount: '',
    interestRate: '',
    minTenure: '1',
    maxTenure: '12',
    processingFee: '0',
    features: '',
    benefits: '',
    eligibility: '',
    description: '',
    isPopular: false,
    category: 'gold',
    order: '999',
    isActive: true,
  });

  const handleCreatePlan = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const planData = {
        name: newPlan.name,
        minAmount: Number(newPlan.minAmount),
        maxAmount: Number(newPlan.maxAmount),
        interestRate: Number(newPlan.interestRate),
        minTenure: Number(newPlan.minTenure),
        maxTenure: Number(newPlan.maxTenure),
        processingFee: Number(newPlan.processingFee),
        features: newPlan.features.split('\n').filter(f => f.trim()),
        benefits: newPlan.benefits.split('\n').filter(f => f.trim()),
        eligibility: newPlan.eligibility.split('\n').filter(f => f.trim()),
        description: newPlan.description,
        isPopular: newPlan.isPopular,
        category: newPlan.category,
        order: Number(newPlan.order),
        isActive: newPlan.isActive,
      };

      const res = await fetch(`${backendBase}/api/admin/loan-plans`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(planData),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to create loan plan');
      }

      setShowCreatePlan(false);
      setNewPlan({
        name: '',
        minAmount: '',
        maxAmount: '',
        interestRate: '',
        minTenure: '1',
        maxTenure: '12',
        processingFee: '0',
        features: '',
        benefits: '',
        eligibility: '',
        description: '',
        isPopular: false,
        category: 'gold',
        order: '999',
        isActive: true,
      });

      // Refresh loan plans list
      const plansRes = await fetch(`${backendBase}/api/admin/loan-plans`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const plansData = await parseJsonSafe(plansRes);
      if (plansData.success) {
        setLoanPlans(plansData.plans || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to create loan plan');
    } finally {
      setLoading(false);
    }
  };

  const handleUpdatePlan = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingPlan) return;

    setError(null);
    setLoading(true);

    try {
      const updateData: any = {};
      if (newPlan.name) updateData.name = newPlan.name;
      if (newPlan.minAmount) updateData.minAmount = Number(newPlan.minAmount);
      if (newPlan.maxAmount) updateData.maxAmount = Number(newPlan.maxAmount);
      if (newPlan.interestRate) updateData.interestRate = Number(newPlan.interestRate);
      if (newPlan.minTenure) updateData.minTenure = Number(newPlan.minTenure);
      if (newPlan.maxTenure) updateData.maxTenure = Number(newPlan.maxTenure);
      if (newPlan.processingFee !== '') updateData.processingFee = Number(newPlan.processingFee);
      if (newPlan.features) updateData.features = newPlan.features.split('\n').filter(f => f.trim());
      if (newPlan.benefits) updateData.benefits = newPlan.benefits.split('\n').filter(f => f.trim());
      if (newPlan.eligibility) updateData.eligibility = newPlan.eligibility.split('\n').filter(f => f.trim());
      if (newPlan.description !== undefined) updateData.description = newPlan.description;
      updateData.isPopular = newPlan.isPopular;
      if (newPlan.category) updateData.category = newPlan.category;
      if (newPlan.order) updateData.order = Number(newPlan.order);
      updateData.isActive = newPlan.isActive;

      const res = await fetch(`${backendBase}/api/admin/loan-plans/${editingPlan._id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(updateData),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to update loan plan');
      }

      setEditingPlan(null);
      setNewPlan({
        name: '',
        minAmount: '',
        maxAmount: '',
        interestRate: '',
        minTenure: '1',
        maxTenure: '12',
        processingFee: '0',
        features: '',
        benefits: '',
        eligibility: '',
        description: '',
        isPopular: false,
        category: 'gold',
        order: '999',
        isActive: true,
      });

      // Refresh loan plans list
      const plansRes = await fetch(`${backendBase}/api/admin/loan-plans`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const plansData = await parseJsonSafe(plansRes);
      if (plansData.success) {
        setLoanPlans(plansData.plans || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to update loan plan');
    } finally {
      setLoading(false);
    }
  };

  const handleEditPlan = (plan: any) => {
    setEditingPlan(plan);
    setNewPlan({
      name: plan.name || '',
      minAmount: plan.minAmount?.toString() || '',
      maxAmount: plan.maxAmount?.toString() || '',
      interestRate: plan.interestRate?.toString() || '',
      minTenure: plan.minTenure?.toString() || '1',
      maxTenure: plan.maxTenure?.toString() || '12',
      processingFee: plan.processingFee?.toString() || '0',
      features: plan.features?.join('\n') || '',
      benefits: plan.benefits?.join('\n') || '',
      eligibility: plan.eligibility?.join('\n') || '',
      description: plan.description || '',
      isPopular: plan.isPopular || false,
      category: plan.category || 'gold',
      order: plan.order?.toString() || '999',
      isActive: plan.isActive !== false,
    });
    setShowCreatePlan(false);
  };

  const handleDeletePlan = async (planId: string) => {
    if (!confirm('Are you sure you want to delete this loan plan?')) return;

    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/loan-plans/${planId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to delete loan plan');
      }

      // Refresh loan plans list
      const plansRes = await fetch(`${backendBase}/api/admin/loan-plans`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const plansData = await parseJsonSafe(plansRes);
      if (plansData.success) {
        setLoanPlans(plansData.plans || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to delete loan plan');
    } finally {
      setLoading(false);
    }
  };

  const handleUpdateInterestRate = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/interest-rate`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ interestRate }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to update interest rate');
      }

      setInterestRate(data.interestRate);
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to update interest rate');
    } finally {
      setLoading(false);
    }
  };

  const handleExport = async () => {
    if (!token) return;

    try {
      // Export filtered data if filters are active, otherwise export all
      const dataToExport = filteredApplications.length < applications.length 
        ? filteredApplications 
        : applications;

      // Create CSV content
      const headers = ['Name', 'Phone', 'LoanAmount', 'Branch', 'CreatedAt'];
      const rows = dataToExport.map((app) => [
        `"${app.name || ''}"`,
        `"${app.phone || ''}"`,
        `"${app.loanAmount || ''}"`,
        `"${app.branch || ''}"`,
        `"${app.createdAt || ''}"`,
      ]);

      const csv = [headers.join(','), ...rows.map((r) => r.join(','))].join('\n');

      // Create blob and download
      const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      
      // Generate filename with filter info if applicable
      let filename = 'quick_applications';
      if (filterBranch || filterLoanAmount) {
        const filterParts = [];
        if (filterBranch) filterParts.push(`branch-${filterBranch.replace(/[^a-zA-Z0-9]/g, '_')}`);
        if (filterLoanAmount) filterParts.push(`amount-${filterLoanAmount.replace(/[^a-zA-Z0-9]/g, '_')}`);
        filename = `quick_applications_filtered_${filterParts.join('_')}`;
      }
      a.download = `${filename}.csv`;
      
      document.body.appendChild(a);
      a.click();
      a.remove();
      window.URL.revokeObjectURL(url);
    } catch (err) {
      console.error(err);
      setError(
        err instanceof Error ? err.message : 'Export failed. Please try again.',
      );
    }
  };

  if (!token) {
    return (
      <main className="min-h-screen flex items-center justify-center relative overflow-hidden bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 p-4">
        {/* Animated Background Elements */}
        <div className="absolute inset-0 overflow-hidden">
          <div className="absolute -top-40 -right-40 w-80 h-80 bg-yellow-500/20 rounded-full blur-3xl animate-pulse"></div>
          <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-purple-500/20 rounded-full blur-3xl animate-pulse" style={{ animationDelay: '1s' }}></div>
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-blue-500/10 rounded-full blur-3xl animate-pulse" style={{ animationDelay: '2s' }}></div>
        </div>

        {/* Grid Pattern Overlay */}
        <div className="absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"></div>

        <div className="relative z-10 w-full max-w-md animate-in fade-in zoom-in duration-500">
          <Card className="backdrop-blur-xl bg-white/98 dark:bg-gray-900/98 border-gray-200/50 dark:border-gray-800/50 shadow-2xl ring-1 ring-gray-900/5 dark:ring-white/10">
            <CardHeader className="space-y-1 text-center pb-8 pt-8">
              <div className="flex justify-center mb-6">
                <div className="relative">
                  <div className="absolute inset-0 bg-yellow-500/30 rounded-2xl blur-2xl animate-pulse"></div>
                  <div className="relative w-20 h-20 rounded-2xl bg-gradient-to-br from-yellow-500 via-yellow-600 to-yellow-700 flex items-center justify-center shadow-2xl shadow-yellow-500/50 transform hover:scale-105 transition-transform duration-300">
                    <Lock className="w-10 h-10 text-white drop-shadow-lg" />
                  </div>
                </div>
              </div>
              <CardTitle className="text-4xl font-extrabold bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 dark:from-white dark:via-gray-100 dark:to-white bg-clip-text text-transparent tracking-tight">
                Admin Login
              </CardTitle>
              <CardDescription className="text-base mt-3 text-gray-600 dark:text-gray-400">
                Welcome back! Please sign in to your account
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6 px-8 pb-8">
              <form onSubmit={handleLogin} className="space-y-6">
                <div className="space-y-2.5">
                  <Label htmlFor="email" className="text-sm font-semibold text-gray-700 dark:text-gray-300 flex items-center gap-2">
                    <Mail className="w-4 h-4" />
                    Email Address
                  </Label>
                  <div className="relative group">
                    <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                      <Mail className="h-5 w-5 text-gray-400 group-focus-within:text-yellow-500 transition-all duration-200" />
                    </div>
                    <Input
                      id="email"
                      type="email"
                      placeholder="admin@yellowmetal.com"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      required
                      className="pl-12 h-12 text-base border-2 border-gray-200 dark:border-gray-700 focus:border-yellow-500 focus:ring-4 focus:ring-yellow-500/20 dark:bg-gray-800/50 transition-all duration-200"
                    />
                  </div>
                </div>
                <div className="space-y-2.5">
                  <Label htmlFor="password" className="text-sm font-semibold text-gray-700 dark:text-gray-300 flex items-center gap-2">
                    <Lock className="w-4 h-4" />
                    Password
                  </Label>
                  <div className="relative group">
                    <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
                      <Lock className="h-5 w-5 text-gray-400 group-focus-within:text-yellow-500 transition-all duration-200" />
                    </div>
                    <Input
                      id="password"
                      type="password"
                      placeholder="Enter your password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      required
                      className="pl-12 h-12 text-base border-2 border-gray-200 dark:border-gray-700 focus:border-yellow-500 focus:ring-4 focus:ring-yellow-500/20 dark:bg-gray-800/50 transition-all duration-200"
                    />
                  </div>
                </div>
                {error && (
                  <div className="p-4 text-sm text-red-700 dark:text-red-400 bg-gradient-to-r from-red-50 to-red-100/50 dark:from-red-900/30 dark:to-red-800/20 border-2 border-red-200 dark:border-red-800/50 rounded-xl flex items-start gap-3 animate-in slide-in-from-top-2 shadow-lg">
                    <svg className="w-5 h-5 flex-shrink-0 mt-0.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <span className="font-medium">{error}</span>
                  </div>
                )}
                <Button
                  type="submit"
                  disabled={loading}
                  className="w-full h-14 text-base font-bold bg-gradient-to-r from-yellow-500 via-yellow-600 to-yellow-500 hover:from-yellow-600 hover:via-yellow-700 hover:to-yellow-600 text-black shadow-2xl shadow-yellow-500/40 hover:shadow-yellow-500/60 transition-all duration-300 disabled:opacity-60 disabled:cursor-not-allowed transform hover:scale-[1.02] active:scale-[0.98]"
                  size="lg"
                >
                  {loading ? (
                    <span className="flex items-center justify-center gap-3">
                      <svg className="animate-spin h-5 w-5 text-black" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      <span>Signing in...</span>
                    </span>
                  ) : (
                    <span className="flex items-center justify-center gap-3">
                      <Lock className="w-5 h-5" />
                      <span>Sign In to Dashboard</span>
                    </span>
                  )}
                </Button>
              </form>
              
              {/* Footer Note */}
              <div className="pt-6 border-t-2 border-gray-100 dark:border-gray-800">
                <div className="flex items-center justify-center gap-2 text-xs text-gray-500 dark:text-gray-400">
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                  </svg>
                  <span className="font-medium">Secure access to Yellow Metal Admin Dashboard</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </main>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 flex">
      {/* Sidebar */}
      <aside className="w-64 bg-gray-900/95 backdrop-blur-xl border-r border-gray-800 flex flex-col">
        {/* Logo/Brand */}
        <div className="p-6 border-b border-gray-800">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-yellow-500 to-yellow-600 flex items-center justify-center shadow-lg shadow-yellow-500/30">
              <Shield className="w-6 h-6 text-white" />
            </div>
            <div>
              <h1 className="text-lg font-bold text-white">Yellow Metal</h1>
              <p className="text-xs text-gray-400">Admin Dashboard</p>
            </div>
          </div>
        </div>

        {/* Navigation */}
        <nav className="flex-1 p-4 space-y-2">
          <button
            onClick={() => setActiveSection('dashboard')}
            className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
              activeSection === 'dashboard'
                ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
            }`}
          >
            <LayoutDashboard className="w-5 h-5" />
            <span className="font-medium">Dashboard</span>
            {activeSection === 'dashboard' && <ChevronRight className="w-4 h-4 ml-auto" />}
          </button>

          <button
            onClick={() => setActiveSection('applications')}
            className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
              activeSection === 'applications'
                ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
            }`}
          >
            <FileText className="w-5 h-5" />
            <span className="font-medium">Applications</span>
            {activeSection === 'applications' && <ChevronRight className="w-4 h-4 ml-auto" />}
          </button>

          {isSuperAdmin && (
            <>
              <button
                onClick={() => setActiveSection('analytics')}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                  activeSection === 'analytics'
                    ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                }`}
              >
                <BarChart3 className="w-5 h-5" />
                <span className="font-medium">Analytics</span>
                {activeSection === 'analytics' && <ChevronRight className="w-4 h-4 ml-auto" />}
              </button>

              <button
                onClick={() => setActiveSection('credentials')}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                  activeSection === 'credentials'
                    ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                }`}
              >
                <Users className="w-5 h-5" />
                <span className="font-medium">Credentials</span>
                {activeSection === 'credentials' && <ChevronRight className="w-4 h-4 ml-auto" />}
              </button>

              <button
                onClick={() => setActiveSection('branches')}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                  activeSection === 'branches'
                    ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                }`}
              >
                <MapPin className="w-5 h-5" />
                <span className="font-medium">Branches</span>
                {activeSection === 'branches' && <ChevronRight className="w-4 h-4 ml-auto" />}
              </button>

              <button
                onClick={() => setActiveSection('loan-plans')}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                  activeSection === 'loan-plans'
                    ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                }`}
              >
                <FileText className="w-5 h-5" />
                <span className="font-medium">Loan Plans</span>
                {activeSection === 'loan-plans' && <ChevronRight className="w-4 h-4 ml-auto" />}
              </button>

              <button
                onClick={() => setActiveSection('settings')}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all ${
                  activeSection === 'settings'
                    ? 'bg-gradient-to-r from-yellow-500/20 to-yellow-600/10 text-yellow-400 border border-yellow-500/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                }`}
              >
                <Settings className="w-5 h-5" />
                <span className="font-medium">Settings</span>
                {activeSection === 'settings' && <ChevronRight className="w-4 h-4 ml-auto" />}
              </button>
            </>
          )}
        </nav>

        {/* User Profile & Logout */}
        <div className="p-4 border-t border-gray-800">
          <div className="flex items-center gap-3 mb-3 p-3 rounded-lg bg-gray-800/50">
            <div className="w-8 h-8 rounded-full bg-gradient-to-br from-yellow-500 to-yellow-600 flex items-center justify-center">
              <span className="text-xs font-bold text-black">
                {userRole === 'super_admin' ? 'SA' : 'A'}
              </span>
            </div>
            <div className="flex-1 min-w-0">
              <p className="text-sm font-medium text-white truncate">
                {decodeToken(token || '')?.email || 'Admin'}
              </p>
              <p className="text-xs text-gray-400">
                {isSuperAdmin ? 'Super Admin' : 'Admin'}
              </p>
            </div>
          </div>
          <button
            onClick={handleLogout}
            className="w-full flex items-center gap-3 px-4 py-3 rounded-lg text-red-400 hover:text-red-300 hover:bg-red-500/10 transition-all"
          >
            <LogOut className="w-5 h-5" />
            <span className="font-medium">Logout</span>
          </button>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 overflow-y-auto">
        <div className="p-6 space-y-6">
          {/* Header */}
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-3xl font-bold text-white">
                {activeSection === 'dashboard' && 'Dashboard'}
                {activeSection === 'applications' && 'Loan Applications'}
                {activeSection === 'analytics' && 'Analytics'}
                {activeSection === 'credentials' && 'Credential Management'}
              </h2>
              <p className="text-gray-400 mt-1">
                {activeSection === 'dashboard' && 'Overview of your loan applications'}
                {activeSection === 'applications' && 'View and manage all loan applications'}
                {activeSection === 'analytics' && 'Detailed analytics and insights'}
                {activeSection === 'credentials' && 'Manage admin user credentials'}
                {activeSection === 'branches' && 'Manage branch locations and pincodes'}
                {activeSection === 'loan-plans' && 'Manage loan plans and interest rates'}
                {activeSection === 'settings' && 'Configure interest rates and system settings'}
              </p>
            </div>
            {activeSection === 'applications' && (
              <Button
                onClick={handleExport}
                className="bg-gradient-to-r from-yellow-500 to-yellow-600 hover:from-yellow-600 hover:to-yellow-700 text-black font-semibold shadow-lg shadow-yellow-500/30"
              >
                <Download className="w-4 h-4 mr-2" />
                Export CSV {filterBranch || filterLoanAmount ? '(Filtered)' : ''}
              </Button>
            )}
          </div>

          {/* Dashboard Section */}
          {activeSection === 'dashboard' && (
            <>
              {/* KPI Cards */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                <Card className="bg-gradient-to-br from-yellow-500/10 to-yellow-600/5 border-yellow-500/20">
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm text-gray-400 mb-1">Total Applications</p>
                        <p className="text-3xl font-bold text-yellow-400">{totalApplications}</p>
                        <p className="text-xs text-gray-500 mt-2">All time</p>
                      </div>
                      <div className="w-12 h-12 rounded-xl bg-yellow-500/20 flex items-center justify-center">
                        <FileText className="w-6 h-6 text-yellow-400" />
                      </div>
                    </div>
                  </CardContent>
                </Card>

                <Card className="bg-gradient-to-br from-green-500/10 to-green-600/5 border-green-500/20">
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm text-gray-400 mb-1">Last 7 Days</p>
                        <p className="text-3xl font-bold text-green-400">{recentApplications}</p>
                        <div className="flex items-center gap-1 mt-2">
                          <TrendingUp className="w-3 h-3 text-green-400" />
                          <p className="text-xs text-green-400">Recent growth</p>
                        </div>
                      </div>
                      <div className="w-12 h-12 rounded-xl bg-green-500/20 flex items-center justify-center">
                        <TrendingUp className="w-6 h-6 text-green-400" />
                      </div>
                    </div>
                  </CardContent>
                </Card>

                <Card className="bg-gradient-to-br from-blue-500/10 to-blue-600/5 border-blue-500/20">
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm text-gray-400 mb-1">Total Loan Amount</p>
                        <p className="text-2xl font-bold text-blue-400">
                          â‚¹{(totalLoanAmount / 100000).toFixed(1)}L
                        </p>
                        <p className="text-xs text-gray-500 mt-2">Cumulative</p>
                      </div>
                      <div className="w-12 h-12 rounded-xl bg-blue-500/20 flex items-center justify-center">
                        <BarChart3 className="w-6 h-6 text-blue-400" />
                      </div>
                    </div>
                  </CardContent>
                </Card>

                {visitorAnalytics && (
                  <Card className="bg-gradient-to-br from-purple-500/10 to-purple-600/5 border-purple-500/20">
                    <CardContent className="p-6">
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="text-sm text-gray-400 mb-1">Total Visitors</p>
                          <p className="text-3xl font-bold text-purple-400">
                            {visitorAnalytics.totalVisitors}
                          </p>
                          <p className="text-xs text-gray-500 mt-2">Website traffic</p>
                        </div>
                        <div className="w-12 h-12 rounded-xl bg-purple-500/20 flex items-center justify-center">
                          <Eye className="w-6 h-6 text-purple-400" />
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )}
              </div>

              {/* Quick Stats */}
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <Card className="bg-gray-900/50 border-gray-800">
                  <CardHeader>
                    <CardTitle className="text-white">Recent Activity</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3">
                      {applications.slice(0, 5).map((app) => (
                        <div key={app._id} className="flex items-center justify-between p-3 rounded-lg bg-gray-800/50">
                          <div>
                            <p className="text-sm font-medium text-white">{app.name}</p>
                            <p className="text-xs text-gray-400">{app.loanAmount}</p>
                          </div>
                          <p className="text-xs text-gray-500">
                            {new Date(app.createdAt).toLocaleDateString('en-IN')}
                          </p>
                        </div>
                      ))}
                    </div>
                  </CardContent>
                </Card>

                {loanAnalytics?.applicationsByBranch && loanAnalytics.applicationsByBranch.length > 0 && (
                  <Card className="bg-gray-900/50 border-gray-800">
                    <CardHeader>
                      <CardTitle className="text-white">Top Pincodes</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-3">
                        {loanAnalytics.applicationsByBranch.slice(0, 5).map((item: any, idx: number) => (
                          <div key={idx} className="flex items-center justify-between p-3 rounded-lg bg-gray-800/50">
                            <div>
                              <p className="text-sm font-medium text-white">{item._id || 'Unknown'}</p>
                              <p className="text-xs text-gray-400">{item.count} applications</p>
                            </div>
                            <div className="w-8 h-8 rounded-full bg-yellow-500/20 flex items-center justify-center">
                              <span className="text-xs font-bold text-yellow-400">{item.count}</span>
                            </div>
                          </div>
                        ))}
                      </div>
                    </CardContent>
                  </Card>
                )}
              </div>
            </>
          )}

          {/* Applications Section */}
          {activeSection === 'applications' && (
            <>
              {/* Filter Section */}
              <Card className="bg-gray-900/50 border-gray-800">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <CardTitle className="text-white flex items-center gap-2">
                      <Filter className="w-5 h-5" />
                      Filter Applications
                    </CardTitle>
                    {(filterBranch || filterLoanAmount) && (
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={handleClearFilters}
                        className="text-gray-300 border-gray-700 hover:bg-gray-800"
                      >
                        <X className="w-4 h-4 mr-2" />
                        Clear Filters
                      </Button>
                    )}
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <Label className="text-gray-300 mb-2">Branch / Pincode</Label>
                      <select
                        value={filterBranch}
                        onChange={(e) => setFilterBranch(e.target.value)}
                        className="w-full px-3 py-2 rounded-md bg-gray-800 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-yellow-500 text-white"
                      >
                        <option value="">All Branches / Pincodes</option>
                        {uniqueBranches.map((branch) => (
                          <option key={branch} value={branch}>
                            {branch}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div>
                      <Label className="text-gray-300 mb-2">Loan Amount</Label>
                      <Input
                        type="text"
                        placeholder="Enter loan amount..."
                        value={filterLoanAmount}
                        onChange={(e) => setFilterLoanAmount(e.target.value)}
                        className="bg-gray-800 border-gray-700 text-white placeholder-gray-500 focus:ring-yellow-500"
                      />
                    </div>
                  </div>
                  {(filterBranch || filterLoanAmount) && (
                    <p className="text-sm text-gray-400 mt-4">
                      Showing {filteredApplications.length} of {applications.length} applications
                    </p>
                  )}
                </CardContent>
              </Card>

              {/* Applications Table */}
              <Card className="bg-gray-900/50 border-gray-800">
                <CardHeader>
                  <CardTitle className="text-white">All Applications</CardTitle>
                </CardHeader>
                <CardContent>
                  {loading ? (
                    <p className="text-gray-400 text-center py-8">Loading applications...</p>
                  ) : filteredApplications.length === 0 ? (
                    <p className="text-gray-400 text-center py-8">
                      {applications.length === 0
                        ? 'No applications found.'
                        : 'No applications match the selected filters.'}
                    </p>
                  ) : (
                    <Table>
                      <TableHeader>
                        <TableRow className="border-gray-800 hover:bg-gray-800/50">
                          <TableHead className="text-gray-300">Name</TableHead>
                          <TableHead className="text-gray-300">Phone</TableHead>
                          <TableHead className="text-gray-300">Loan Amount</TableHead>
                          <TableHead className="text-gray-300">Branch / Pincode</TableHead>
                          <TableHead className="text-gray-300">Created At</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {filteredApplications.map((app) => (
                          <TableRow key={app._id} className="border-gray-800 hover:bg-gray-800/30">
                            <TableCell className="text-white font-medium">{app.name}</TableCell>
                            <TableCell className="text-gray-300">{app.phone}</TableCell>
                            <TableCell className="text-yellow-400 font-semibold">{app.loanAmount}</TableCell>
                            <TableCell className="text-gray-300">{app.branch}</TableCell>
                            <TableCell className="text-gray-400">
                              {new Date(app.createdAt).toLocaleString('en-IN')}
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  )}
                </CardContent>
              </Card>
            </>
          )}

          {/* Analytics Section - Only for Super Admin */}
          {activeSection === 'analytics' && isSuperAdmin && (
            <>
            <div className="flex items-center justify-between mb-5">
              <h2 className="text-xl font-bold text-white">Analytics Dashboard</h2>
              <span className="text-xs text-gray-400">Real-time Insights</span>
            </div>

            {analyticsLoading ? (
              <div className="flex items-center justify-center py-12">
                <div className="text-gray-400">Loading analytics...</div>
              </div>
            ) : (
              <div className="space-y-6">
                {/* Key Metrics */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                  {loanAnalytics && (
                    <>
                      <Card className="bg-gradient-to-br from-yellow-500/10 to-yellow-600/5 border-yellow-500/20">
                        <CardContent className="p-6">
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="text-sm text-gray-400 mb-1">Total Applications</p>
                              <p className="text-3xl font-bold text-yellow-400">
                                {loanAnalytics.totalApplications}
                              </p>
                            </div>
                            <FileText className="w-8 h-8 text-yellow-400/50" />
                          </div>
                        </CardContent>
                      </Card>
                      <Card className="bg-gradient-to-br from-green-500/10 to-green-600/5 border-green-500/20">
                        <CardContent className="p-6">
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="text-sm text-gray-400 mb-1">Last 7 Days</p>
                              <p className="text-3xl font-bold text-green-400">
                                {loanAnalytics.recentApplications}
                              </p>
                            </div>
                            <TrendingUp className="w-8 h-8 text-green-400/50" />
                          </div>
                        </CardContent>
                      </Card>
                      <Card className="bg-gradient-to-br from-blue-500/10 to-blue-600/5 border-blue-500/20">
                        <CardContent className="p-6">
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="text-sm text-gray-400 mb-1">Total Amount</p>
                              <p className="text-2xl font-bold text-blue-400">
                                â‚¹{(loanAnalytics.totalLoanAmount / 100000).toFixed(1)}L
                              </p>
                            </div>
                            <BarChart3 className="w-8 h-8 text-blue-400/50" />
                          </div>
                        </CardContent>
                      </Card>
                    </>
                  )}
                  {visitorAnalytics && (
                    <Card className="bg-gradient-to-br from-purple-500/10 to-purple-600/5 border-purple-500/20">
                      <CardContent className="p-6">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="text-sm text-gray-400 mb-1">Total Visitors</p>
                            <p className="text-3xl font-bold text-purple-400">
                              {visitorAnalytics.totalVisitors}
                            </p>
                          </div>
                          <Eye className="w-8 h-8 text-purple-400/50" />
                        </div>
                      </CardContent>
                    </Card>
                  )}
                </div>

                {/* Charts Grid */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  {/* Daily Registrations Chart */}
                  {loanAnalytics?.applicationsByDate && loanAnalytics.applicationsByDate.length > 0 && (
                    <Card className="bg-gray-900/50 border-gray-800">
                      <CardHeader>
                        <CardTitle className="text-white">Daily Registrations</CardTitle>
                        <CardDescription>Applications over the last 14 days</CardDescription>
                      </CardHeader>
                      <CardContent>
                        <div className="h-64">
                          <ResponsiveContainer width="100%" height="100%">
                            <LineChart
                              data={loanAnalytics.applicationsByDate.slice(-14).map((item: any) => ({
                                date: new Date(item._id).toLocaleDateString('en-IN', { month: 'short', day: 'numeric' }),
                                count: item.count,
                              }))}
                              margin={{ top: 5, right: 10, left: -10, bottom: 5 }}
                            >
                              <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.3} />
                              <XAxis dataKey="date" stroke="#9ca3af" style={{ fontSize: '11px' }} />
                              <YAxis stroke="#9ca3af" style={{ fontSize: '11px' }} />
                              <Tooltip
                                contentStyle={{
                                  backgroundColor: '#1f2937',
                                  border: '1px solid #374151',
                                  borderRadius: '6px',
                                  fontSize: '12px',
                                  color: '#fff',
                                }}
                              />
                              <Line
                                type="monotone"
                                dataKey="count"
                                stroke="#fbbf24"
                                strokeWidth={3}
                                dot={{ fill: '#fbbf24', r: 4 }}
                              />
                            </LineChart>
                          </ResponsiveContainer>
                        </div>
                      </CardContent>
                    </Card>
                  )}

                  {/* Applications by Pincode - Bar Chart */}
                  {loanAnalytics?.applicationsByBranch && loanAnalytics.applicationsByBranch.length > 0 && (
                    <Card className="bg-gray-900/50 border-gray-800">
                      <CardHeader>
                        <CardTitle className="text-white">Top Pincodes</CardTitle>
                        <CardDescription>Applications by location</CardDescription>
                      </CardHeader>
                      <CardContent>
                        <div className="h-64">
                          <ResponsiveContainer width="100%" height="100%">
                            <BarChart
                              data={loanAnalytics.applicationsByBranch.slice(0, 6).map((item: any) => ({
                                name: item._id || 'Unknown',
                                count: item.count,
                              }))}
                              margin={{ top: 5, right: 10, left: -10, bottom: 40 }}
                            >
                              <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.3} />
                              <XAxis
                                dataKey="name"
                                stroke="#9ca3af"
                                angle={-45}
                                textAnchor="end"
                                height={60}
                                style={{ fontSize: '11px' }}
                              />
                              <YAxis stroke="#9ca3af" style={{ fontSize: '11px' }} />
                              <Tooltip
                                contentStyle={{
                                  backgroundColor: '#1f2937',
                                  border: '1px solid #374151',
                                  borderRadius: '6px',
                                  fontSize: '12px',
                                  color: '#fff',
                                }}
                              />
                              <Bar dataKey="count" fill="#fbbf24" radius={[4, 4, 0, 0]} />
                            </BarChart>
                          </ResponsiveContainer>
                        </div>
                      </CardContent>
                    </Card>
                  )}
                </div>

                {/* Website Visitors Section */}
                {visitorAnalytics && (
                  <Card className="bg-gray-900/50 border-gray-800">
                    <CardHeader>
                      <CardTitle className="text-white">Website Traffic</CardTitle>
                      <CardDescription>Visitor analytics overview</CardDescription>
                    </CardHeader>
                    <CardContent>
                      <div className="grid grid-cols-3 gap-4">
                        <div className="p-4 bg-gray-800/50 rounded-lg text-center border border-gray-700">
                          <p className="text-xs text-gray-400 mb-2">Total</p>
                          <p className="text-2xl font-bold text-yellow-400">
                            {visitorAnalytics.totalVisitors}
                          </p>
                        </div>
                        <div className="p-4 bg-gray-800/50 rounded-lg text-center border border-gray-700">
                          <p className="text-xs text-gray-400 mb-2">Unique</p>
                          <p className="text-2xl font-bold text-blue-400">
                            {visitorAnalytics.uniqueVisitors}
                          </p>
                        </div>
                        <div className="p-4 bg-gray-800/50 rounded-lg text-center border border-gray-700">
                          <p className="text-xs text-gray-400 mb-2">Return Rate</p>
                          <p className="text-2xl font-bold text-green-400">
                            {visitorAnalytics.totalVisitors > 0
                              ? Math.round(
                                  ((visitorAnalytics.totalVisitors - visitorAnalytics.uniqueVisitors) /
                                    visitorAnalytics.totalVisitors) * 100
                                )
                              : 0}%
                          </p>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )}
              </div>
            )}
            </>
          )}

          {/* Credentials Section - Only for Super Admin */}
          {activeSection === 'credentials' && isSuperAdmin && (
            <Card className="bg-gray-900/50 border-gray-800">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-white">Admin Credential Management</CardTitle>
                    <CardDescription>Manage admin user accounts and permissions</CardDescription>
                  </div>
                  {!showCreateForm && !editingUser && (
                    <Button
                      onClick={() => setShowCreateForm(true)}
                      className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                    >
                      <UserPlus className="w-4 h-4 mr-2" />
                      Create New Admin
                    </Button>
                  )}
                </div>
              </CardHeader>
              <CardContent className="space-y-6">

                {showCreateForm && !editingUser && (
                  <Card className="bg-gray-800/50 border-gray-700">
                    <CardHeader>
                      <CardTitle className="text-white">Create New Admin User</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <form onSubmit={handleCreateUser} className="space-y-4">
                        <div>
                          <Label className="text-gray-300 mb-2">Email</Label>
                          <Input
                            type="email"
                            value={newUserEmail}
                            onChange={(e) => setNewUserEmail(e.target.value)}
                            required
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="admin@example.com"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Password</Label>
                          <Input
                            type="password"
                            value={newUserPassword}
                            onChange={(e) => setNewUserPassword(e.target.value)}
                            required
                            minLength={6}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="Minimum 6 characters"
                          />
                          <p className="text-xs text-gray-400 mt-1">Password must be at least 6 characters</p>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            type="submit"
                            disabled={loading}
                            className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                          >
                            {loading ? 'Creating...' : 'Create Admin'}
                          </Button>
                          <Button
                            type="button"
                            variant="outline"
                            onClick={() => {
                              setShowCreateForm(false);
                              setNewUserEmail('');
                              setNewUserPassword('');
                            }}
                            className="border-gray-700 text-gray-300 hover:bg-gray-800"
                          >
                            Cancel
                          </Button>
                        </div>
                      </form>
                    </CardContent>
                  </Card>
                )}

                {editingUser && (() => {
                  const isSuperAdminUser = editingUser.email === 'admin@yellowmetal.com' || editingUser.role === 'super_admin';
                  return (
                    <Card className="bg-gray-800/50 border-gray-700">
                      <CardHeader>
                        <CardTitle className="text-white">
                          Edit Admin User
                          {isSuperAdminUser && (
                            <span className="ml-2 text-sm text-yellow-400 font-normal">(Super Admin - Limited Editing)</span>
                          )}
                        </CardTitle>
                      </CardHeader>
                      <CardContent>
                        <form onSubmit={handleUpdateUser} className="space-y-4">
                          <div>
                            <Label className="text-gray-300 mb-2">Email</Label>
                            <Input
                              type="email"
                              value={editUserEmail}
                              onChange={(e) => setEditUserEmail(e.target.value)}
                              disabled={isSuperAdminUser}
                              required
                              className={`bg-gray-800 border-gray-700 text-white focus:ring-yellow-500 ${
                                isSuperAdminUser ? 'opacity-60 cursor-not-allowed' : ''
                              }`}
                            />
                            {isSuperAdminUser && (
                              <p className="text-xs text-gray-400 mt-1">Super admin email cannot be changed</p>
                            )}
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">New Password (leave blank to keep current)</Label>
                            <Input
                              type="password"
                              value={editUserPassword}
                              onChange={(e) => setEditUserPassword(e.target.value)}
                              minLength={6}
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="Enter new password or leave blank"
                            />
                            <p className="text-xs text-gray-400 mt-1">Leave blank to keep current password</p>
                          </div>
                          <div className="flex items-center gap-2">
                            <input
                              type="checkbox"
                              checked={editUserActive}
                              onChange={(e) => setEditUserActive(e.target.checked)}
                              disabled={isSuperAdminUser}
                              className="rounded"
                            />
                            <Label className={`text-sm ${isSuperAdminUser ? 'text-gray-500' : 'text-gray-300'}`}>
                              Active
                              {isSuperAdminUser && ' (Cannot deactivate super admin)'}
                            </Label>
                          </div>
                          <div className="flex gap-2">
                            <Button
                              type="submit"
                              disabled={loading}
                              className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800"
                            >
                              {loading ? 'Updating...' : 'Update'}
                            </Button>
                            <Button
                              type="button"
                              variant="outline"
                              onClick={() => {
                                setEditingUser(null);
                                setEditUserEmail('');
                                setEditUserPassword('');
                              }}
                              className="border-gray-700 text-gray-300 hover:bg-gray-800"
                            >
                              Cancel
                            </Button>
                          </div>
                        </form>
                      </CardContent>
                    </Card>
                  );
                })()}

                {/* Admin Users Table */}
                <Card className="bg-gray-900/50 border-gray-800">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <CardTitle className="text-white">All Admin Users</CardTitle>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={async () => {
                          if (!token) return;
                          try {
                            setAdminUsersLoading(true);
                            setError(null);
                            const res = await fetch(`${backendBase}/api/admin/users`, {
                              headers: {
                                Authorization: `Bearer ${token}`,
                              },
                            });
                            const data = await parseJsonSafe(res);
                            if (res.ok && data.success) {
                              setAdminUsers(data.users || []);
                            } else {
                              throw new Error(data.error || 'Failed to refresh admin users');
                            }
                          } catch (err) {
                            console.error(err);
                            setError(err instanceof Error ? err.message : 'Failed to refresh admin users');
                          } finally {
                            setAdminUsersLoading(false);
                          }
                        }}
                        disabled={adminUsersLoading}
                        className="border-gray-700 text-gray-300 hover:bg-gray-800"
                      >
                        {adminUsersLoading ? (
                          <>
                            <svg className="animate-spin -ml-1 mr-2 h-4 w-4" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                              <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                            </svg>
                            Refreshing...
                          </>
                        ) : (
                          <>
                            <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                            Refresh
                          </>
                        )}
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent>
                    {adminUsersLoading && adminUsers.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">Loading admin users...</div>
                      </div>
                    ) : adminUsers.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">No admin users found.</div>
                      </div>
                    ) : (
                      <Table>
                      <TableHeader>
                        <TableRow className="border-gray-800 hover:bg-gray-800/50">
                          <TableHead className="text-gray-300">Email</TableHead>
                          <TableHead className="text-gray-300">Role</TableHead>
                          <TableHead className="text-gray-300">Created At</TableHead>
                          <TableHead className="text-gray-300">Status</TableHead>
                          <TableHead className="text-gray-300">Actions</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {adminUsers.map((user) => {
                          const isSuperAdminUser = user.email === 'admin@yellowmetal.com' || user.role === 'super_admin';
                          return (
                            <TableRow key={user._id} className="border-gray-800 hover:bg-gray-800/30">
                              <TableCell className="text-white">
                                {user.email}
                                {isSuperAdminUser && (
                                  <span className="ml-2 text-xs text-yellow-400">(Super Admin)</span>
                                )}
                              </TableCell>
                              <TableCell>
                                <span className={`px-2 py-1 rounded text-xs ${
                                  isSuperAdminUser 
                                    ? 'bg-yellow-900/50 text-yellow-300 border border-yellow-500/30' 
                                    : 'bg-blue-900/50 text-blue-300 border border-blue-500/30'
                                }`}>
                                  {isSuperAdminUser ? 'Super Admin' : 'Admin'}
                                </span>
                              </TableCell>
                              <TableCell className="text-gray-300">
                                {new Date(user.createdAt).toLocaleString('en-IN')}
                              </TableCell>
                              <TableCell>
                                <span className={`px-2 py-1 rounded text-xs ${
                                  user.isActive 
                                    ? 'bg-green-900/50 text-green-300 border border-green-500/30' 
                                    : 'bg-red-900/50 text-red-300 border border-red-500/30'
                                }`}>
                                  {user.isActive ? 'Active' : 'Inactive'}
                                </span>
                              </TableCell>
                              <TableCell>
                                {isSuperAdminUser ? (
                                  <span className="text-xs text-gray-500 flex items-center gap-1">
                                    <Shield className="w-3 h-3" />
                                    Protected
                                  </span>
                                ) : (
                                  <div className="flex gap-2">
                                    <Button
                                      size="sm"
                                      variant="outline"
                                      onClick={() => handleEditUser(user)}
                                      className="border-blue-600 text-blue-400 hover:bg-blue-600/10"
                                    >
                                      <Edit className="w-3 h-3 mr-1" />
                                      Edit
                                    </Button>
                                    <Button
                                      size="sm"
                                      variant="outline"
                                      onClick={() => handleDeleteUser(user._id)}
                                      className="border-red-600 text-red-400 hover:bg-red-600/10"
                                    >
                                      <Trash2 className="w-3 h-3 mr-1" />
                                      Delete
                                    </Button>
                                  </div>
                                )}
                              </TableCell>
                            </TableRow>
                          );
                        })}
                      </TableBody>
                    </Table>
                    )}
                  </CardContent>
                </Card>
              </CardContent>
            </Card>
          )}

          {/* Branch Management Section - Only for Super Admin */}
          {activeSection === 'branches' && isSuperAdmin && (
            <Card className="bg-gray-900/50 border-gray-800">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-white">Branch Management</CardTitle>
                    <CardDescription>Manage branch locations and pincodes for application form</CardDescription>
                  </div>
                  {!showCreateBranch && (
                    <Button
                      onClick={() => setShowCreateBranch(true)}
                      className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                    >
                      <Plus className="w-4 h-4 mr-2" />
                      Add New Branch
                    </Button>
                  )}
                </div>
              </CardHeader>
              <CardContent className="space-y-6">
                {showCreateBranch && (
                  <Card className="bg-gray-800/50 border-gray-700">
                    <CardHeader>
                      <CardTitle className="text-white">Add New Branch</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <form onSubmit={handleCreateBranch} className="space-y-4">
                        <div>
                          <Label className="text-gray-300 mb-2">Town Name</Label>
                          <Input
                            type="text"
                            value={newBranchTown}
                            onChange={(e) => setNewBranchTown(e.target.value)}
                            required
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="e.g. Bangalore"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Pincode</Label>
                          <Input
                            type="text"
                            value={newBranchPincode}
                            onChange={(e) => setNewBranchPincode(e.target.value)}
                            required
                            pattern="[0-9]{6}"
                            maxLength={6}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="e.g. 560001"
                          />
                          <p className="text-xs text-gray-400 mt-1">Enter 6-digit pincode</p>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            type="submit"
                            disabled={loading}
                            className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                          >
                            {loading ? 'Adding...' : 'Add Branch'}
                          </Button>
                          <Button
                            type="button"
                            variant="outline"
                            onClick={() => {
                              setShowCreateBranch(false);
                              setNewBranchTown('');
                              setNewBranchPincode('');
                            }}
                            className="border-gray-700 text-gray-300 hover:bg-gray-800"
                          >
                            Cancel
                          </Button>
                        </div>
                      </form>
                    </CardContent>
                  </Card>
                )}

                {/* Branches Table */}
                <Card className="bg-gray-900/50 border-gray-800">
                  <CardHeader>
                    <CardTitle className="text-white">All Branches</CardTitle>
                  </CardHeader>
                  <CardContent>
                    {branchesLoading && branches.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">Loading branches...</div>
                      </div>
                    ) : branches.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">No branches found.</div>
                      </div>
                    ) : (
                      <Table>
                        <TableHeader>
                          <TableRow className="border-gray-800 hover:bg-gray-800/50">
                            <TableHead className="text-gray-300">Town</TableHead>
                            <TableHead className="text-gray-300">Pincode</TableHead>
                            <TableHead className="text-gray-300">Created At</TableHead>
                            <TableHead className="text-gray-300">Actions</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {branches.map((branch) => (
                            <TableRow key={branch._id} className="border-gray-800 hover:bg-gray-800/30">
                              <TableCell className="text-white font-medium">{branch.town}</TableCell>
                              <TableCell className="text-yellow-400 font-semibold">{branch.pincode}</TableCell>
                              <TableCell className="text-gray-300">
                                {new Date(branch.createdAt).toLocaleString('en-IN')}
                              </TableCell>
                              <TableCell>
                                <Button
                                  size="sm"
                                  variant="outline"
                                  onClick={() => handleDeleteBranch(branch._id)}
                                  className="border-red-600 text-red-400 hover:bg-red-600/10"
                                >
                                  <Trash2 className="w-3 h-3 mr-1" />
                                  Delete
                                </Button>
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    )}
                  </CardContent>
                </Card>
              </CardContent>
            </Card>
          )}

          {/* Loan Plans Management Section - Only for Super Admin */}
          {activeSection === 'loan-plans' && isSuperAdmin && (
            <Card className="bg-gray-900/50 border-gray-800">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-white">Loan Plans Management</CardTitle>
                    <CardDescription>Manage loan plans displayed on the loan page</CardDescription>
                  </div>
                  {!showCreatePlan && !editingPlan && (
                    <Button
                      onClick={() => setShowCreatePlan(true)}
                      className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                    >
                      <Plus className="w-4 h-4 mr-2" />
                      Add New Plan
                    </Button>
                  )}
                </div>
              </CardHeader>
              <CardContent className="space-y-6">
                {(showCreatePlan || editingPlan) && (
                  <Card className="bg-gray-800/50 border-gray-700">
                    <CardHeader>
                      <CardTitle className="text-white">
                        {editingPlan ? 'Edit Loan Plan' : 'Create New Loan Plan'}
                      </CardTitle>
                    </CardHeader>
                    <CardContent>
                      <form onSubmit={editingPlan ? handleUpdatePlan : handleCreatePlan} className="space-y-4">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          <div>
                            <Label className="text-gray-300 mb-2">Plan Name *</Label>
                            <Input
                              value={newPlan.name}
                              onChange={(e) => setNewPlan({ ...newPlan, name: e.target.value })}
                              required
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="e.g. Gold Loan Express"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Category *</Label>
                            <select
                              value={newPlan.category}
                              onChange={(e) => setNewPlan({ ...newPlan, category: e.target.value })}
                              className="w-full px-3 py-2 rounded-md bg-gray-800 border border-gray-700 text-white focus:ring-yellow-500"
                            >
                              <option value="gold">Gold</option>
                              <option value="property">Property</option>
                              <option value="fixed-deposit">Fixed Deposit</option>
                            </select>
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Min Amount (â‚¹) *</Label>
                            <Input
                              type="number"
                              value={newPlan.minAmount}
                              onChange={(e) => setNewPlan({ ...newPlan, minAmount: e.target.value })}
                              required
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="10000"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Max Amount (â‚¹) *</Label>
                            <Input
                              type="number"
                              value={newPlan.maxAmount}
                              onChange={(e) => setNewPlan({ ...newPlan, maxAmount: e.target.value })}
                              required
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="1000000"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Interest Rate (%) *</Label>
                            <Input
                              type="number"
                              step="0.1"
                              value={newPlan.interestRate}
                              onChange={(e) => setNewPlan({ ...newPlan, interestRate: e.target.value })}
                              required
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="12.0"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Processing Fee (%)</Label>
                            <Input
                              type="number"
                              step="0.1"
                              value={newPlan.processingFee}
                              onChange={(e) => setNewPlan({ ...newPlan, processingFee: e.target.value })}
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="0.5"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Min Tenure (months)</Label>
                            <Input
                              type="number"
                              value={newPlan.minTenure}
                              onChange={(e) => setNewPlan({ ...newPlan, minTenure: e.target.value })}
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="1"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Max Tenure (months)</Label>
                            <Input
                              type="number"
                              value={newPlan.maxTenure}
                              onChange={(e) => setNewPlan({ ...newPlan, maxTenure: e.target.value })}
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="12"
                            />
                          </div>
                          <div>
                            <Label className="text-gray-300 mb-2">Order</Label>
                            <Input
                              type="number"
                              value={newPlan.order}
                              onChange={(e) => setNewPlan({ ...newPlan, order: e.target.value })}
                              className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                              placeholder="999"
                            />
                          </div>
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Description</Label>
                          <Input
                            value={newPlan.description}
                            onChange={(e) => setNewPlan({ ...newPlan, description: e.target.value })}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="Plan description"
                          />
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Features (one per line)</Label>
                          <Textarea
                            value={newPlan.features}
                            onChange={(e) => setNewPlan({ ...newPlan, features: e.target.value })}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="Instant approval&#10;Minimal documentation"
                            rows={4}
                          />
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Benefits (one per line)</Label>
                          <Textarea
                            value={newPlan.benefits}
                            onChange={(e) => setNewPlan({ ...newPlan, benefits: e.target.value })}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="Quick disbursal&#10;Competitive rates"
                            rows={4}
                          />
                        </div>
                        <div>
                          <Label className="text-gray-300 mb-2">Eligibility (one per line)</Label>
                          <Textarea
                            value={newPlan.eligibility}
                            onChange={(e) => setNewPlan({ ...newPlan, eligibility: e.target.value })}
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500"
                            placeholder="Age: 18-70 years&#10;Valid ID proof"
                            rows={4}
                          />
                        </div>
                        <div className="flex items-center gap-4">
                          <label className="flex items-center gap-2">
                            <input
                              type="checkbox"
                              checked={newPlan.isPopular}
                              onChange={(e) => setNewPlan({ ...newPlan, isPopular: e.target.checked })}
                              className="rounded"
                            />
                            <span className="text-sm text-gray-300">Mark as Popular</span>
                          </label>
                          <label className="flex items-center gap-2">
                            <input
                              type="checkbox"
                              checked={newPlan.isActive}
                              onChange={(e) => setNewPlan({ ...newPlan, isActive: e.target.checked })}
                              className="rounded"
                            />
                            <span className="text-sm text-gray-300">Active</span>
                          </label>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            type="submit"
                            disabled={loading}
                            className="bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
                          >
                            {loading ? 'Saving...' : editingPlan ? 'Update Plan' : 'Create Plan'}
                          </Button>
                          <Button
                            type="button"
                            variant="outline"
                            onClick={() => {
                              setShowCreatePlan(false);
                              setEditingPlan(null);
                              setNewPlan({
                                name: '',
                                minAmount: '',
                                maxAmount: '',
                                interestRate: '',
                                minTenure: '1',
                                maxTenure: '12',
                                processingFee: '0',
                                features: '',
                                benefits: '',
                                eligibility: '',
                                description: '',
                                isPopular: false,
                                category: 'gold',
                                order: '999',
                                isActive: true,
                              });
                            }}
                            className="border-gray-700 text-gray-300 hover:bg-gray-800"
                          >
                            Cancel
                          </Button>
                        </div>
                      </form>
                    </CardContent>
                  </Card>
                )}

                {/* Loan Plans Table */}
                <Card className="bg-gray-900/50 border-gray-800">
                  <CardHeader>
                    <CardTitle className="text-white">All Loan Plans</CardTitle>
                  </CardHeader>
                  <CardContent>
                    {loanPlansLoading && loanPlans.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">Loading loan plans...</div>
                      </div>
                    ) : loanPlans.length === 0 ? (
                      <div className="flex items-center justify-center py-12">
                        <div className="text-gray-400">No loan plans found.</div>
                      </div>
                    ) : (
                      <Table>
                        <TableHeader>
                          <TableRow className="border-gray-800 hover:bg-gray-800/50">
                            <TableHead className="text-gray-300">Name</TableHead>
                            <TableHead className="text-gray-300">Amount Range</TableHead>
                            <TableHead className="text-gray-300">Interest Rate</TableHead>
                            <TableHead className="text-gray-300">Category</TableHead>
                            <TableHead className="text-gray-300">Status</TableHead>
                            <TableHead className="text-gray-300">Actions</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {loanPlans.map((plan) => (
                            <TableRow key={plan._id} className="border-gray-800 hover:bg-gray-800/30">
                              <TableCell className="text-white font-medium">
                                {plan.name}
                                {plan.isPopular && (
                                  <span className="ml-2 text-xs text-yellow-400">(Popular)</span>
                                )}
                              </TableCell>
                              <TableCell className="text-gray-300">
                                â‚¹{plan.minAmount?.toLocaleString('en-IN')} - â‚¹{plan.maxAmount?.toLocaleString('en-IN')}
                              </TableCell>
                              <TableCell className="text-yellow-400 font-semibold">{plan.interestRate}%</TableCell>
                              <TableCell>
                                <span className="px-2 py-1 rounded text-xs bg-blue-900/50 text-blue-300 border border-blue-500/30 capitalize">
                                  {plan.category}
                                </span>
                              </TableCell>
                              <TableCell>
                                <span className={`px-2 py-1 rounded text-xs ${
                                  plan.isActive 
                                    ? 'bg-green-900/50 text-green-300 border border-green-500/30' 
                                    : 'bg-red-900/50 text-red-300 border border-red-500/30'
                                }`}>
                                  {plan.isActive ? 'Active' : 'Inactive'}
                                </span>
                              </TableCell>
                              <TableCell>
                                <div className="flex gap-2">
                                  <Button
                                    size="sm"
                                    variant="outline"
                                    onClick={() => handleEditPlan(plan)}
                                    className="border-blue-600 text-blue-400 hover:bg-blue-600/10"
                                  >
                                    <Edit className="w-3 h-3 mr-1" />
                                    Edit
                                  </Button>
                                  <Button
                                    size="sm"
                                    variant="outline"
                                    onClick={() => handleDeletePlan(plan._id)}
                                    className="border-red-600 text-red-400 hover:bg-red-600/10"
                                  >
                                    <Trash2 className="w-3 h-3 mr-1" />
                                    Delete
                                  </Button>
                                </div>
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    )}
                  </CardContent>
                </Card>
              </CardContent>
            </Card>
          )}

          {/* Settings Section - Only for Super Admin */}
          {activeSection === 'settings' && isSuperAdmin && (
            <Card className="bg-gray-900/50 border-gray-800">
              <CardHeader>
                <CardTitle className="text-white">System Settings</CardTitle>
                <CardDescription>Configure interest rates and system-wide settings</CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <Card className="bg-gray-800/50 border-gray-700">
                  <CardHeader>
                    <CardTitle className="text-white">Default Interest Rate</CardTitle>
                    <CardDescription>This rate is used in the loan calculator on the loan page</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <form onSubmit={handleUpdateInterestRate} className="space-y-4">
                      <div>
                        <Label className="text-gray-300 mb-2">Interest Rate (% per annum) *</Label>
                        <div className="flex gap-4 items-center">
                          <Input
                            type="number"
                            step="0.1"
                            value={interestRate}
                            onChange={(e) => setInterestRate(Number(e.target.value))}
                            required
                            className="bg-gray-800 border-gray-700 text-white focus:ring-yellow-500 w-32"
                            placeholder="9.0"
                          />
                          <span className="text-gray-400">% per annum</span>
                        </div>
                        <p className="text-xs text-gray-400 mt-2">
                          This rate will be displayed in the loan calculator on the loan page
                        </p>
                      </div>
                      <Button
                        type="submit"
                        disabled={loading || interestRateLoading}
                        className="bg-gradient-to-r from-yellow-600 to-yellow-700 hover:from-yellow-700 hover:to-yellow-800"
                      >
                        {loading ? 'Updating...' : 'Update Interest Rate'}
                      </Button>
                    </form>
                  </CardContent>
                </Card>
              </CardContent>
            </Card>
          )}

          {error && (
            <Card className="bg-red-900/20 border-red-800">
              <CardContent className="p-4">
                <p className="text-sm text-red-400">{error}</p>
              </CardContent>
            </Card>
          )}
        </div>
      </main>
    </div>
  );
}


